<template>
	<setup
		title="Reset Your Password"
		:error="error"
		:hidePageTitle="hidePageTitle"
	>
		<div class="otp-sending-progress" v-cloak v-if="sendingOtp">
			<div>
				Sending OTP code to your
				{{ otpToEmail ? "email address" : "contact number" }}...<span
					style="white-space: nowrap"
					>Please wait</span
				>
			</div>
		</div>
		<div class="otp-sending-progress" v-cloak v-if="verifyingOtp">
			<div>
				Verifying confirmation code...<span style="white-space: nowrap"
					>Please wait</span
				>
			</div>
		</div>
		<form method="post" @submit.prevent="handleFormSubmit">
			<div
				class="default-form w-border"
				style="text-align: center; margin: auto"
			>
				<p class="p-activation-note">
					<strong>Confirm your identity</strong> by entering the
					confirmation code
				</p>

				<p>
					Confirmation code sent to
					<span>{{ otpToEmail ? email : mobileNumber }}</span>
				</p>
				<div class="form-row">
					<div class="otp-control">
						<v-otp-input
							ref="otpInput"
							input-classes="otp-input"
							separator=""
							:num-inputs="6"
							:should-auto-focus="true"
							:is-input-num="false"
							@on-change="handleOtpChange"
							@on-complete="handleOtpComplete"
						/>
					</div>
				</div>
				<div class="form-row">
					<div
						class="spinner small light"
						v-show="verifyingOtp"
					></div>
					<input
						type="submit"
						value="Verify Code"
						class="button large blue"
						v-show="!verifyingOtp"
						v-cloak
					/>
				</div>
				<div class="form-row">
					<a style="cursor: pointer" v-on:click="sendOtp"
						>Send me a new code</a
					>
					{{ " or" }}<br />
					<span v-cloak v-if="email && !otpToEmail"
						><a
							style="cursor: pointer"
							v-on:click="handleSwitchOtpReceiver"
							>Send code through my email ({{ email }})</a
						>{{ " or" }} <br
					/></span>

					<span v-cloak v-if="mobileNumber && otpToEmail"
						><a
							style="cursor: pointer"
							v-on:click="handleSwitchOtpReceiver"
							>Send code through my contact number ({{
								mobileNumber
							}})</a
						>
						or <br
					/></span>
					<a style="cursor: pointer" v-on:click="handleGoToQuestions"
						>Answer my Security Questions</a
					>
				</div>
			</div>
		</form>
	</setup>
</template>

<script>
	import Setup from "./Setup.vue";
	import { getErrorMessage } from "../remote/ajax";
	export default {
		name: "OTP",
		props: [
			"sentToEmail",
			"email",
			"mobileNumber",
			"memberId",
			"token",
			"redirectUrl",
		],
		data() {
			return {
				otpToEmail: this.$props.sentToEmail,
				sendingOtp: false,
				verifyingOtp: false,
				code: "",
				error: "",
				hidePageTitle: false,
			};
		},
		methods: {
			handleFormSubmit() {
				if (!this.code || this.code.length < 6) {
					this.error = `Insert the confirmation code that was sent to your
						${
							this.otpToEmail
								? "email address (" + this.email + ")"
								: "contact number (" + this.mobileNumber + ")"
						}`;
					return false;
				}

				this.error = "";
				const url = this.otpToEmail
					? "/Password/VerifyEmailOTP"
					: "/Password/VerifySMSOTP";
				this.verifyingOtp = true;
				this.$ajax
					.post(url, {
						memberId: this.memberId,
						verificationCode: this.code,
						token: this.token,
					})
					.then((resp) => {
						console.log(resp);
						this.verifyingOtp = false;
						const error = getErrorMessage(
							resp.data.result,
							"VERIFY_OTP",
						);
						if (error === "") {
							const params = {
								userId: this.memberId,
								token: resp.data.token,
								redirectUrl: this.redirectUrl,
							};
							this.$router.push({
								name: "SetNew",
								params: params,
							});
						} else {
							this.error = error;
							this.$refs.otpInput.clearInput();
						}
					})
					.finally(() => (this.verifyingOtp = false));
			},
			handleOtpChange(value) {
				this.code = value;
			},
			handleOtpComplete(value) {
				this.code = value;
			},
			sendOtp() {
				const url = this.otpToEmail
					? "/Password/SendOTPToEmail"
					: "/Password/SendOTPToSMS";
				this.sendingOtp = true;
				this.$ajax
					.get(url, {
						params: { memberId: this.memberId, token: this.token },
					})
					.then((resp) => {
						const error = getErrorMessage(resp.result, "SEND_OTP");
						if (error) {
							this.error = error;
						}
					})
					.finally(() => (this.sendingOtp = false));
			},
			handleSwitchOtpReceiver() {
				this.otpToEmail = !this.otpToEmail;
				this.sendOtp();
			},
			handleGoToQuestions() {
				this.$router.push({
					name: "SetupQuestions",
					params: {
						memberId: this.memberId,
						token: this.token,
						redirectUrl: this.redirectUrl,
						email: this.email,
						sentToEmail: this.sentToEmail,
						mobileNumber: this.mobileNumber,
					},
				});
			},
		},
		components: {
			Setup,
		},
		mounted() {
			if (!this.memberId) {
				this.$router.push({
					name: "Recover",
					query: { redirectUrl: this.$route.query.redirectUrl },
				});
			} else {
				this.sendOtp();
			}
		},
	};
</script>
