import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import VTooltip from "v-tooltip";
import Ajax from "./remote/ajax";
import OtpInput from "@bachdgvn/vue-otp-input";

import "./styles/app.scss";

Vue.component("v-otp-input", OtpInput);
Vue.use(Ajax, {
    handleUnautorized: true
});
Vue.use(VTooltip);

Vue.prototype.$reformatUrl = function(pageName) {
    let redirectRequired = false;
    let search = window.location.search;
    if (search != null) {
        let queryString = search.split("?")[1];
        if (typeof queryString != "undefined") {
            redirectRequired = true;
        }
    }
    if (redirectRequired) {
        window.location.href = "/#/" + pageName + search;
    }
};

Vue.prototype.$validateEmail = function(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
};

Vue.config.productionTip = false;

new Vue({
    router,
    render: h => h(App)
}).$mount("#app");
