<script>
	import Setup from "./Setup.vue";
	import PasswordPage from "./PasswordPage.vue";
	import { getErrorMessage } from "../remote/ajax";
	//TODO: run a client-side check to see if token and userid is present. If not, just don't display the page. Or error...
	export default {
		name: "SetNew",
		props: ["pageSubtitle", "userId", "token", "redirectUrl"],
		components: {
			PasswordPage,
		},
		data() {
			return {
				error: "",
				loading: false,
				success: "",
				output: {
					password: "",
					confirmPassword: "",
				},
			};
		},
		components: {
			Setup,
		},
		methods: {
			handleFormSubmit() {
				let data = {
					Password: this.output.password,
					ConfirmPassword: this.output.confirmPassword,
					UserId: this.userId,
					Token: this.token,
				};

				this.error = "";
				if (data.Password == "" && data.ConfirmPassword == "") {
					this.error = "Password fields cannot be blank.";
				} else if (data.ConfirmPassword == "") {
					this.error = "Confirm Password field cannot be blank.";
				} else if (data.Password == "") {
					this.error = "Password field cannot be blank.";
				} else if (data.Password != data.ConfirmPassword) {
					this.error = "Password fields do not match.";
				}
				if (this.error != "") return;

				this.loading = true;

				this.$ajax
					.post("/Password/SetNew", data)
					.then((resp) => {
						if (typeof resp.data != "undefined") {
							const error = getErrorMessage(resp.data.result);
							if (error) {
								this.error = resp.data.errorMsg;
								this.loading = false;
							} else {
								const __this = this;
								this.success =
									"You have successfully reset your password. You will be redirected to Login page in 5 seconds.";
								setTimeout(() => {
									__this.$router.push({
										name: "Login",
										query: this.redirectUrl
											? { redirectUrl: this.redirectUrl }
											: undefined,
									});
								}, 5000);
							}
						}
					})
					.catch(
						() =>
							(this.error =
								"Oops! Something went wrong...Please try again after 5 minutes."),
					);
			},
		},
	};
</script>

<template>
	<setup title="Set New Password" :error="error" :success="success">
		<form method="post" @submit.prevent="handleFormSubmit">
			<h3>{{ pageSubtitle }}</h3>
			<passwordPage :loading="loading" v-model="output"></passwordPage>
		</form>
	</setup>
</template>
